<template>
  <a-layout id="components-layout-demo-top" class="layout">
    <a-layout-header>
      <div class="logo" />
      <a-menu
        @click="changeTab"
        theme="dark"
        mode="horizontal"
        :default-selected-keys="['1']"
        :style="{ lineHeight: '64px' }"
      >
        <a-menu-item key="1">
          扫描网址
        </a-menu-item>
        <a-menu-item key="3">
          登陆/注册
        </a-menu-item>
        <a-menu-item key="5">
          网站分布地图
        </a-menu-item>
        <a-menu-item key="2" v-if="showNormal">
          网址申述/举报
        </a-menu-item>
        <a-menu-item v-if="showAdmin" key="4">
          审批申述
        </a-menu-item>
      </a-menu>
    </a-layout-header>
    <keep-alive include="table">
      <router-view></router-view>
    </keep-alive>

    <a-layout-footer style="text-align: center">
      彗眼 ©2020 Created by Intelligence
      <br />
      技术支持:
      <a-icon type="ant-design" :style="{ fontSize: '25px', color: '#08c' }" />
    </a-layout-footer>
  </a-layout>
</template>
<script>
export default {
  data() {
    return {
      breadcrumb: ["扫描网址", "网址申述", "登陆"],
      breadtext: "扫描网址",
      inputUrl: "",
      showImg: true,
      showTable: false,
      tableData: [],
      showSpin: false,
    };
  },
  computed: {
    showAdmin() {
      return this.$store.state.loginState ? true : false;
    },
    showNormal() {
      return this.$store.state.normalState ? true: false;
    }
  },
  methods: {
    changeTab(e) {
      switch (e.key) {
        case "2":
          this.breadtext = this.breadcrumb[1];
          this.$router.push({ name: "Judge" });
          break;
        case "3":
          this.breadtext = this.breadcrumb[2];
          this.$router.push({ name: "Login" });
          break;
        case "4":
          this.$router.push({ name: "Handler" });
          break;
        case "5":
          this.$router.push({ name: "Map" });
          break;
        default:
          this.breadtext = this.breadcrumb[0];
          this.$router.push({ name: "Index" });
          break;
      }
    },
  },
};
</script>
<style>
.layout {
  height: 100%;
}
#components-layout-demo-top .logo {
  width: 120px;
  height: 31px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px 24px 16px 0;
  float: left;
}
</style>
